.photosWidth {
  width: 614px;
  margin-left: 20px;
}
.previewPhotos {
  width: 100%;
  margin-left: 20px;
}
.documentsWidth {
  width: 694px;
  margin-left: 20px;
}
.documentPreviews {
    width: 100%;
  margin-left: 20px;
}
.photosForm {
  padding: 5px;
  .contentPart {
    padding: 10px;
    margin: auto;
    height: 65vh;
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
  }

  .contentPart::-webkit-scrollbar {
    width: 0.3em;
  }

  .contentPart::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(91, 90, 90, 0.2);
    border-radius: 5%;
  }

  .contentPart::-webkit-scrollbar-thumb {
    background-color: rgb(209, 204, 204);
    outline: 0.2px solid rgb(183, 186, 189);
    border-radius: 5%;
  }
  hr {
    border-top: 2px solid #e8e9e9;
  }
  .btn {
    column-gap: 17px;
  }
  .btnWidth {
    width: 160px;
  }
  .uploadtext {
    color: #4b5153;
    font-family: Barlow;
    font-size: 14px;
    font-weight: 500;
    text-align: left;
  }
  .mb {
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0em;
    text-align: left;
    color: #4b5153 !important;
  }
}
.title {
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  font-family: "Inter";
  color: #090909;
  margin-bottom: 16px;
}

.customfile {
  border: 1.5px solid #e8e9e9;
  border-radius: 16px;
  // width: 580px;
  .label {
    height: 90px;
    width: 100%;
    padding: 24px;
    cursor: pointer !important;
    display: flex;
    gap: 17px;
    position: relative;
    margin-bottom: 0px !important;
    cursor: pointer;
    .uploadtext {
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      font-family: "Barlow";
      color: #4b5153;
    }
    .uploadbtn {
      font-size: 14px !important;
      font-weight: 500 !important;
      // padding: 11px 26px;
      position: absolute;
      z-index: 2 !important;
      right: 24px;
      top: 0;
      bottom: 0;
      margin: auto;
      justify-content: center;
      align-items: center;
      border-radius: 10px;
      color: #935ed6 !important;
      background-color: #fff;
      display: inline-table;
      text-decoration: none;
    }
    .format {
      font-size: 12px;
      font-weight: 400;
      line-height: 18px;
      color: #787c7e !important;
      .mb {
        font-size: 12px;
        font-weight: 400;
        line-height: 18px;
        letter-spacing: 0em;
        text-align: left;
        color: #4b5153 !important;
      }
    }
  }
}

.customdoc {
  border: 1.5px solid #e8e9e9;
  padding: 22px;
  display: flex;
  align-items: center;
  gap: 17px;
  border-radius: 16px;
  .doclabel {
    margin-bottom: 0px;
    .uploadbtn {
      font-size: 14px !important;
      font-weight: 500 !important;
      line-height: 20px;
      font-family: "Barlow";
      padding: 11px 26px;
      border: 1.5px solid#1E2528;
      z-index: 2 !important;
      right: 24px;
      top: 0;
      bottom: 0;
      margin: auto;
      border-radius: 10px;
      color: #1e2528 !important;
      background-color: #fafbfb;
      display: inline-table;
      text-decoration: none;
    }
  }
}

.documentSelect {
  width: 100%;
  .document_select {
    :global {
      .document_select__control {
        background-color: red;
      }
    }
  }
}
